.collection {
    text-align: center;    

    &__slider{
        margin-bottom: 67px;
        position: relative;
        margin-inline: 20px;


        &-main {
            position: relative;
        }

        &-list {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 50px;
            column-gap: 20px;

            
        }

        &-item {
            // flex-shrink: 0;
            
        }
        
        &-button {
            width: 40px;
            aspect-ratio: 1;
            border-radius: 50%;
            background-color: #fff;
            color: #b7b4bb;
            vertical-align: baseline;
            position: absolute;
            top: 40%;
            right: -20px;

            & .svg-wrapper {
                width: 10px;
            }

            &--inactive {
                opacity: 75%;
            }
        }
    }
}